<template>
    <div class="BMI">
        <el-tabs v-model="activeName">
            <el-tab-pane label="中国BMI指数图" name="first"> </el-tab-pane>
            <el-tab-pane label="体育项目BMI对比图" name="second"> </el-tab-pane>
        </el-tabs>
        <el-carousel ref="carousel" class="carousel" direction="vertical" :autoplay="false" indicator-position="none" arrow="never">
            <el-carousel-item>
                <iframe
                    ref="iframe"
                    src="https://flo.uri.sh/visualisation/5695286/embed"
                    title="Interactive or visual content"
                    frameborder="0"
                    scrolling="no"
                    style="width:100%;height:600px;"
                    sandbox="allow-same-origin allow-forms allow-scripts allow-downloads allow-popups allow-popups-to-escape-sandbox allow-top-navigation-by-user-activation"
                ></iframe>
                <div style="width:100%!;margin-top:4px!important;text-align:right!important;">
                    <a
                        class="flourish-credit"
                        href="https://public.flourish.studio/visualisation/5695286/?utm_source=embed&utm_campaign=visualisation/5695286"
                        target="_top"
                        style="text-decoration:none!important"
                    >
                        <img
                            alt="Made with Flourish"
                            src="https://public.flourish.studio/resources/made_with_flourish.svg"
                            style="width:105px!important;height:16px!important;border:none!important;margin:0!important;"
                        />
                    </a>
                </div>
            </el-carousel-item>
            <el-carousel-item>
                <el-switch style="display: block" v-model="value" active-color="#67C23A" inactive-color="#409eff" active-text="标准差标准化" inactive-text="最大最小值标准化"> </el-switch>
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
    import { animate } from "@/function/animate.js";
    export default {
        props: [],
        mounted() {
            this.$refs.iframe.addEventListener("load", function(e) {
                animate(e.target, "fadeIn");
            });
        },
        data() {
            return {
                value: true,
                activeName: "first",
            };
        },
        watch: {
            activeName(val) {
                switch (val) {
                    case "first":
                        this.$refs.carousel.setActiveItem(0);
                        break;
                    case "second":
                        this.$refs.carousel.setActiveItem(1);
                        break;
                }
            },
        },
    };
</script>
<style lang="less">
    .BMI {
        height: 100vh;
        display: flex;
        flex-direction: column;
        width: 80vw;
        margin: 0 auto;
        .carousel {
            height: 100%;
            flex: 1;
            .el-carousel__container {
                height: 100%;
            }
        }
    }
</style>
